@media (min-width: 1200px) and (max-width: 1800px) {
  .header {
    padding-left: 30px;
    padding-right: 30px;
    &__tel {
      white-space: nowrap;
    }
  }
  .header-menu {
    flex-wrap: wrap;
    margin-left: 20px;
    &__item {
      flex-basis: 33.333%;
      margin-bottom: 15px;
      margin-left: 0px;
    }
  }
}
@media (max-width: 1199px) {
  .header {
    padding-right: 0;
    padding-left: 0;
    margin-bottom: 20px;
    &__menu {
      order: 2;
      flex-grow: 1;
    }
    &__call {
      padding-top: 20px;
      margin-left: 0;
      position: fixed;
      z-index: 20;
      width: 100%;
      left: 0;
      top: 0;
      transform: translateY(-100%);
      transition: .3s;
    }
    &__socials {
      margin-left: 100%;
    }
  }
  .header-menu {
    position: fixed;
    left: 0; top: 0;
    width: 100%;
    height: 100%;
    background-color: #13560d;
    flex-direction: column;
    justify-content: space-between;
    z-index: 20;
    padding-top: 130px;
    padding-bottom: 40px;
    font-size: 24px;
    transform: translateY(-100%);
    transition: .3s;
    &__item {
      margin-left: 0;
    }
  }
  .burger-btn {
    display: block;
  }
  .thanks-page-wrapper {
    & .header {
      color: #fff;
    }
    & .burger-btn--active {
      span, span::before, span::after {
        background-color: #fff;
      }
    }
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .features-content {
    flex-direction: column;
    align-items: center;
    &__circle {
      margin-bottom: 50px;
    }
    &__col {
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-between;
    }
    &__item {
      flex-basis: 45%;
      position: static;
      flex-direction: column;
      margin-bottom: 50px;
      & span {
        text-align: center;
      }
    }
  }
  .faq {
    background-position-y: -280px;
    background-size: 150% 135%;
    padding-top: 150px;
    padding-bottom: 50px;
    &__inner {
      padding-left: 150px;
      padding-right: 150px;
      flex-direction: column;
    }
    &__questions {
      flex-basis: auto;
      margin-right: 0;
    }
    &__title {
      margin-bottom: 25px;
    }
    &__form {
      flex-basis: auto;
    }
  }
  .questions-list {
    &__item {
      padding-right: 60px;
    }
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .header {
    &__socials {
      margin-left: 50%;
    }
  }
  .first-screen {
    &__content {
      align-items: flex-start;
    }
  }
  .catalog-list {
    flex-wrap: wrap;
    &__item {
      flex-basis: 45%;
      margin-bottom: 40px;
    }
  }
  .features-content {
    flex-direction: column;
    align-items: center;
    &__circle {
      margin-bottom: 50px;
    }
    &__col {
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-between;
    }
    &__item {
      flex-basis: 45%;
      position: static;
      flex-direction: column;
      margin-bottom: 50px;
      & span {
        text-align: center;
      }
    }
  }
  .faq {
    background-position-y: -300px;
    padding-top: 150px;
    background-size: 150% 130%;
    &__inner {
      padding-left: 50px;
      padding-right: 50px;
      flex-direction: column;
    }
    &__questions {
      flex-basis: auto;
      margin-right: 0;
    }
    &__title {
      margin-bottom: 25px;
    }
    &__form {
      flex-basis: auto;
    }
  }
  .questions-list {
    &__item {
      padding-right: 60px;
    }
  }
}
@media (max-width: 767px) {
  .main-content {
    background-image: url(../../img/bg/bg4.png);
    background-repeat: no-repeat;
    background-size: auto;
    background-position: 40% 3000px;
  }
  .header {
    &__socials {
      margin-left: 0;
    }
    &__tel {
      font-size: 20px;
    }
  }
  .contact-form {
    padding: 20px 20px 25px;
  }
  .main-content {
    border-top-right-radius: 150px 100px;
  }

  .first-screen {
    padding-left: 15px;
    padding-right: 15px;
    &__inner {
      padding: 35px 15px;
    }
    &__title {
      font-size: 29px;
      line-height: 29px;
    }
    &__features {
      margin-bottom: 300px;
    }
    &__img {
      right: 5%;
      top: 250px;
    }
  }
  .catalog {
    padding: 40px 0;
    &__inner {
      flex-direction: column;
      padding-left: 25px;
      padding-right: 25px;
    }
    &__title {
      margin-bottom: 30px;
    }
  }
  .catalog-list {
    flex-direction: column;
    &__item {
      flex-basis: auto;
      flex-direction: row;
      margin-bottom: 12px;
    }
    &__img {
      min-height: auto;
    }
  }
  .form-pricing {
    width: 80%;
  }
  .features {
    margin-bottom: 50px;
    &__inner {
      padding-left: 10px;
      padding-right: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  .features-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    &__circle {
      width: 270px;
      height: 270px;
      margin-top: 0;
      padding-top: 0;
      justify-content: center;
      background-image: none;
    }
    &__item {
      position: static;
      flex-direction: column;
      margin-bottom: 50px;
      & span {
        max-width: 300px;
        text-align: center;
      }
      & img {
        max-width: 50%;
      }
    }
  }
  .certificates {
    margin-bottom: 50px;
    &__inner {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  .faq {
    background-position-y: -350px;
    background-size: 450% 140%;
    padding-top: 150px;
    padding-bottom: 0;
    &__inner {
      padding-left: 8px;
      padding-right: 8px;
      flex-direction: column;
    }
    &__questions {
      flex-basis: auto;
      margin-right: 0;
    }
    &__title {
      margin-bottom: 25px;
    }
    &__form {
      flex-basis: auto;
    }
  }
  .questions-list {
    &__item {
      padding-right: 60px;
    }
  }
  .footer {
    padding: 0;
    padding-top: 10px;
    &__inner {
      flex-direction: column;
    }
    &__logo {
      align-items: center;
      flex-direction: column;
      margin-bottom: 25px;
      & > img {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }
    &__logo-text {
      text-align: center;
      margin-bottom: 20px;
    }
  }

  .thanks {
    &__inner {
      padding-left: 15px;
      padding-right: 15px;
    }
    &__content {
      padding: 40px 40px 200px;
    }
  }
}

@media (max-width: 480px) {
  .socials {
    &__item {
      width: 40px;
      height: 40px;
    }
  }
  .burger-btn {
    margin-right: 10px;
  }
  .header {
    &__logo-text {
      visibility: hidden;
    }
  }
  .btn {
    font-size: 26px;
  }
  .contact-form {
    &__title {
      font-size: 18px;
      line-height: 26px;
    }
    &__subtitle {
      font-size: 13px;
      line-height: 17px;
    }
    &__label {
      font-size: 10px;
    }
    &--light-text &__title {
      font-size: 28px;
    }
  }
  .title {
    font-size: 30px;
    &::before {
      bottom: 10px;
      left: -30px;
    }
  }
  .first-screen {
    &__title {
      font-size: 29px;
      line-height: 29px;
    }
    &__features-item {
      font-size: 17px;
      padding-left: 50px;
      &::before {
        width: 35px;
        height: 35px;
        bottom: -10px;
      }
    }
    &__img {
      top: 250px;
    }
  }
  .features-content {
    &__circle {
      & span {
        font-size: 18px;
      }
    }
    &__item {
      & span {
        font-size: 18px;
        max-width: 300px;
      }
    }
  }
  .form-pricing {
    width: 100%;
  }
  .certificates {
    &__inner {
      padding-left: 0;
      padding-right: 0;
    }
    &__item {
      margin-left: 10px;
      margin-right: 10px;
    }
  }
  .faq {
    &__title {
      font-size: 24px;
    }
  }
  .questions-list {
    &__item {
      font-size: 12px;
    }
  }
  .thanks {
    &__content {
      padding: 40px 40px 50px;
    }
    &__title {
      font-size: 30px;
    }
  }
}
